"use client";

import { useState, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { CheckCircle, CreditCard, Shield, Lightning } from "@phosphor-icons/react";
import Image from "next/image";

export default function HomePage() {
	const [amount, setAmount] = useState("");
	const [serviceDescription, setServiceDescription] = useState("");
	const [paymentReason, setPaymentReason] = useState("");
	const [showSuccess, setShowSuccess] = useState(false);
	const router = useRouter();
	const searchParams = useSearchParams();

	useEffect(() => {
		// Check if user was redirected back after successful payment
		const redirectStatus = searchParams.get('redirect_status');
		const paymentIntent = searchParams.get('payment_intent');
		
		if (redirectStatus === 'succeeded' && paymentIntent) {
			setShowSuccess(true);
			// Clean up URL after showing success
			window.history.replaceState({}, '', '/');
		}
	}, [searchParams]);

	const handleProceed = () => {
		const numAmount = parseFloat(amount);
		if (numAmount && numAmount > 0) {
			const params = new URLSearchParams({
				amount: numAmount.toString(),
				service: serviceDescription,
				reason: paymentReason
			});
			router.push(`/checkout?${params.toString()}`);
		}
	};

	const handleNewPayment = () => {
		setShowSuccess(false);
		setAmount("");
	};

	if (showSuccess) {
		return (
			<div className="min-h-screen w-full bg-gradient-to-br from-emerald-50 to-teal-50">
				{/* Header */}
				<div className="bg-white shadow-sm border-b">
					<div className="max-w-7xl mx-auto px-4 py-4">
						<div className="flex items-center justify-center">
							<Image
								src="/abc.png"
								alt="Logo"
								width={180}
								height={60}
								className="h-12 w-auto"
							/>
						</div>
					</div>
				</div>

				{/* Success Content */}
				<div className="py-20 px-4">
					<div className="mx-auto max-w-lg text-center">
						<div className="bg-white rounded-2xl shadow-xl p-8 border border-emerald-100">
							<div className="mb-8">
								<div className="w-20 h-20 bg-emerald-100 rounded-full flex items-center justify-center mx-auto mb-6">
									<CheckCircle
										className="w-12 h-12 text-emerald-600"
										weight="fill"
									/>
								</div>
								<h1 className="text-3xl font-bold mb-4 text-gray-900">
									Payment Successful!
								</h1>
								<p className="text-lg text-gray-600 mb-6">
									Your payment has been processed successfully. Thank you for your business!
								</p>
								<div className="bg-emerald-50 rounded-lg p-4 mb-6">
									<p className="text-emerald-800 font-medium">
										Transaction completed securely
									</p>
								</div>
							</div>

							<Button
								onClick={handleNewPayment}
								className="w-full h-12 text-lg bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white rounded-lg font-medium"
							>
								Make Another Payment
							</Button>
						</div>
					</div>
				</div>
			</div>
		);
	}

	return (
		<div className="min-h-screen w-full bg-gradient-to-br from-blue-50 via-white to-orange-50">
			{/* Header */}
			<div className="bg-white shadow-sm border-b">
				<div className="max-w-7xl mx-auto px-4 py-4">
					<div className="flex items-center justify-center">
						<Image
							src="/abc.png"
							alt="Logo"
							width={180}
							height={60}
							className="h-12 w-auto"
						/>
					</div>
				</div>
			</div>

			{/* Main Content */}
			<div className="py-20 px-4">
				<div className="mx-auto max-w-lg text-center">
					{/* Hero Section */}
					<div className="mb-12">
						<h1 className="text-4xl sm:text-5xl font-bold mb-4 text-gray-900">
							Secure <span className="text-blue-600">Payment</span>
						</h1>
						<p className="text-xl text-gray-600 mb-8">
							Enter your payment amount and proceed to secure checkout
						</p>
					</div>

					{/* Payment Form */}
					<div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
						<div className="space-y-6">
							{/* Service Description */}
							<div>
								<label className="block text-sm font-medium text-gray-700 mb-2 text-left">
									What service are you providing?
								</label>
								<Input
									type="text"
									placeholder="e.g., Website Development, Consulting, Design Services..."
									value={serviceDescription}
									onChange={(e) => setServiceDescription(e.target.value)}
									className="h-12 border-2 border-gray-200 rounded-lg focus:border-blue-500 focus:ring-blue-500"
								/>
							</div>

							{/* Payment Reason */}
							<div>
								<label className="block text-sm font-medium text-gray-700 mb-2 text-left">
									What is the reason for this payment?
								</label>
								<textarea
									placeholder="e.g., Payment for completed project, Monthly retainer, Initial deposit..."
									value={paymentReason}
									onChange={(e) => setPaymentReason(e.target.value)}
									className="w-full h-20 p-3 border-2 border-gray-200 rounded-lg focus:border-blue-500 focus:ring-blue-500 resize-none"
								/>
							</div>

							{/* Payment Amount */}
							<div>
								<label className="block text-sm font-medium text-gray-700 mb-2 text-left">
									Payment Amount
								</label>
								<div className="relative">
									<span className="absolute left-4 top-1/2 transform -translate-y-1/2 text-xl font-semibold text-gray-600">
										$
									</span>
									<Input
										type="number"
										placeholder="0.00"
										value={amount}
										onChange={(e) => setAmount(e.target.value)}
										className="pl-10 text-xl h-14 border-2 border-gray-200 rounded-lg focus:border-blue-500 focus:ring-blue-500"
										min="1"
										step="0.01"
									/>
								</div>
							</div>

							<Button
								onClick={handleProceed}
								disabled={!amount || parseFloat(amount) <= 0}
								className="w-full h-14 text-lg bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed"
							>
								<CreditCard className="w-5 h-5 mr-2" />
								Proceed to Secure Checkout
							</Button>
						</div>

						{/* Features */}
						<div className="mt-8 pt-6 border-t border-gray-100">
							<div className="grid grid-cols-3 gap-4 text-center">
								<div className="flex flex-col items-center">
									<Shield className="w-6 h-6 text-blue-600 mb-2" />
									<span className="text-xs text-gray-600">Secure</span>
								</div>
								<div className="flex flex-col items-center">
									<Lightning className="w-6 h-6 text-blue-600 mb-2" />
									<span className="text-xs text-gray-600">Fast</span>
								</div>
								<div className="flex flex-col items-center">
									<CheckCircle className="w-6 h-6 text-blue-600 mb-2" />
									<span className="text-xs text-gray-600">Trusted</span>
								</div>
							</div>
						</div>
					</div>

					{/* Trust Indicators */}
					<div className="mt-8 text-center">
						<p className="text-sm text-gray-500">
							Powered by Stripe • SSL Encrypted • PCI Compliant
						</p>
					</div>
				</div>
			</div>
		</div>
	);
}